div#CreateAccForm {
    display: flex;
    flex-direction: column;
    align-items: center; 
}



div#FullName {
    display: flex;
    flex-direction: row;
}

div.FieldInput {
    margin-top: 2.2rem;
    font-size: 1.4rem;
    margin-left: 1.5rem;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

.InputBox {
    height: 2rem;
    margin-left: 1rem;
    border-radius: 5px;
    border-width: 1px;
}

div#AccSubmit {
    margin-top: 1rem;
    margin-left: 3rem;
    width: 10rem;
}

button#SubmitButton {
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
    border-radius: 20px;
    border-width: 1px;
    height: 1.5rem;
}


